function GroupedTables(props){
    return props.activities.map ((group, index) => {
      return (
        <div key={index}>
        <div>
          {group.group}
        </div>
        <Table striped bordered condensed hover>
          <tbody>
            {group.records.map ((details, index) =>
            <RecordListDetail
              key={index}
              details={details}
            />
            )}
          </tbody>
        </Table>
      </div>)})}


export default function CollapsibleTable(props) {
 
  console.log("token in activiti table ",props.token.teamleaders[0].attributes.TeamLeader)
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell align="right"> </TableCell>
            <TableCell align="right">" </TableCell>
            
          </TableRow>
        </TableHead>
        <TableBody text-align="right">
          {props.token.teamleaders.map((row) => (
            <Row text-align="right" key={row.attributes.TeamLeader} row={row}token />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}