import  { Component } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import PropTypes from 'prop-types';
import { red } from 'material-ui-colors';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import ErorList from './errorlist'
import configData from "./config.json";


const useStyles = makeStyles((theme) => ({

  bgc_red:{
 backgroundColor: red['A700'],
 color:'white'
  }
  
}));

async function loginUser(credentials) {
  console.log("credentials",credentials)
 return fetch(configData.server+'login', {
   method: 'POST',
   headers: {
     'Content-Type': 'application/json'
   },
   body: JSON.stringify(credentials)
 })
   .then(data => data.json())

}
export default function Logout(props) {
  const classes = useStyles();
  const [username, setUserName] = useState();
  const [password, setPassword] = useState();
let err=null
  const handleSubmit = async e => {
    e.preventDefault();
    let token={}
    console.log(token,"token after catchhhhhh")
    props.setToken(token);
    console.log("after cat")
  }
console.log("err in log in",props.err)


  return (
    <Container  component="main" maxWidth="xs" align="right">
    
        <form onSubmit={handleSubmit} className={classes.form} noValidate>
         
         
           <Stack sx={{ width: '100%' }} spacing={2}>  
       
          <Button
             margin="normal"
            type="submit"
            fullWidth
            variant="contained"
           
            className={classes.submit,classes.bgc_red}
          >
            
          </Button>
        </Stack>
        </form>
      
      
    </Container>
  );
}
Logout.propTypes = {
  setToken: PropTypes.func.isRequired
};
