
import React, { useState } from 'react'
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({

  bgc_grey: {
    
    backgroundColor: '#bdbdbd',
  },
}));

function Row(props) {
  return (
    <React.Fragment>
      <TableRow  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
        <TableCell  width="20%"align="right">
         {props.activiti.Name}
        </TableCell>
        <TableCell  width="20%"align="right">
         {props.activiti.Date.slice(0,10)}
        </TableCell>
        <TableCell  width="20%"align="right">
         {props.activiti.Week}
        </TableCell>
        <TableCell  width="20%"align="right">
         {props.activiti.Sum}
        </TableCell>
        <TableCell  width="20%"align="right">
         {props.activiti.StatusDesc}
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}
export default function ActivitiesTablenehemanot(props) {
  console.log("token in activititablenehemanot table ",props)
  const [open, setOpen] = React.useState(false);
  const classes = useStyles();
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow  sx={{ '& > *': { borderBottom: 'unset' } }}>
          <TableCell align="right" component="th" scope="row" width="10%">
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => {
              console.log("onclick")
              setOpen(!open)
              }} >
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
            <TableCell align="right" component="th" scope="row" width="30%">{props.nehemenet.Name}</TableCell>
            <TableCell  align="right" component="th" scope="row" width="30%"> {props.nehemenet.Sum}</TableCell>
            <TableCell  align="right" component="th" scope="row" width="30%"> {props.nehemenet.Count}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody text-align="right">
          <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
          <TableContainer component={Paper}>
      <Table  sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow sx={{ fontWeight: 'bold', m: 1 }}>
            <TableCell width="20%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right"></TableCell>
            <TableCell width="20%"className={classes.bgc_grey}sx={{ fontWeight: 'bold', m: 1 }}align="right"></TableCell>
            <TableCell  width="20%"className={classes.bgc_grey}sx={{ fontWeight: 'bold', m: 1 }}align="right">' </TableCell>
            <TableCell  width="20%"className={classes.bgc_grey}sx={{ fontWeight: 'bold', m: 1 }}align="right"></TableCell>
            <TableCell  width="20%"className={classes.bgc_grey}sx={{ fontWeight: 'bold', m: 1 }}align="right"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody text-align="right">
          {props.nehemenet.activities.map((row) => (
              <Row  activiti={row.attributes}></Row>
            ))}
        </TableBody>
      </Table>
    </TableContainer>
          </Collapse>
        </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}