import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import CollapsibleTable from'./activitiesTable'
import RewardsTable from './rewardstable'
import { red } from 'material-ui-colors';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function BasicTabs(props) {
 // console.log("in tab component ",props.token)
  const [value, setValue] = React.useState(0);
 // const { teamleaders, setTeamleaders } = useTeamleaders();
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
 
  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange}   textColor="black"
        TabIndicatorProps={{style: {background:red['A700']}}}
        >
          <Tab label="Item One" {...a11yProps(0)} />
          <Tab label="" {...a11yProps(1)} />
          <Tab label="" {...a11yProps(2)} />
          <Tab label="Item Three" {...a11yProps(3)} />
          <Tab label="Item Three" {...a11yProps(4)} />
          <Tab label="Item Three" {...a11yProps(5)} />
          <Tab label="Item Three" {...a11yProps(6)} />
          <Tab label="Item Three" {...a11yProps(7)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        Item One
      </TabPanel>
      <TabPanel value={value} index={1}>
        <CollapsibleTable token={props.token}></CollapsibleTable>
      </TabPanel>
      <TabPanel value={value} index={2}>
      <RewardsTable  token={props.token}></RewardsTable>
      </TabPanel>
      <TabPanel value={value} index={3}>
        Item Three
      </TabPanel>
      <TabPanel value={value} index={4}>
        Item Three
      </TabPanel>
      <TabPanel value={value} index={5}>
        Item Three
      </TabPanel>
      <TabPanel value={value} index={6}>
        Item Three
      </TabPanel>
      <TabPanel value={value} index={7}>
        Item Three
      </TabPanel>
    </Box>
  );
}