import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import Alert from '../errorlist'
import configData from "../config.json";
export default class Getactivities extends Component{
    constructor(props){
      super(props);
      this.state = {
        topics: [],
        topics2: [],
        err:null
      }
    }
  
    componentDidMount() {
      fetch(configData.server+'getBonusActivities', {
        method: 'POST',
        
      }).then((res) => {
        if (res.ok) {
          res.json().then((data)=>{
            this.setState({ topics: data.activities['Activities']['Activity'] });
             this.creathierarchy()
             console.log("before render",this.state.topics)
            this.render()
          })
        } else {
          throw new Error('Something went wrong');
        }
      })
      .then((responseJson) => {
        // Do something with the response
      })
      .catch((error) => {
        console.log("error.message",error.message)
        this.setState({err:error.message+' http://localhost:8080/getBonusActivities'})
        this.render()
      });











      /*fetch('http://localhost:8080/getBonusActivities', {
        method: 'POST',
        
      }).then((res)=>res.json().then((data)=>{
        this.setState({ topics: data.activities['Activities']['Activity'] });
         this.creathierarchy()
         console.log("before render",this.state.topics)
        this.render()
      }));*/
      console.log(this.state.topics) //returns [];
     
    }
    f(name,creathierarchy){
      let i=-1
      creathierarchy.map((Trustee,index)=>{
        if(Trustee.TrusteeName===name)
          i=index
      })
      return i
    }
    creathierarchy(){
      const creathierarchy=[]
      this.state.topics.map((act)=>{
        const i=null
       
      const index=this.f(act['$']['TrusteeName'],creathierarchy)
       if(creathierarchy.length>0&&index!=-1){
      
        creathierarchy[index]["act"].push(act['$'])
      }
        else{
          creathierarchy.push({TrusteeName:act['$']['TrusteeName'],act:[act['$']]})
        }
      }
      )
     this.setState({topics2:creathierarchy})
    return creathierarchy
    }
    render() {
      
      
     // console.log("mmmmmmmmmmmmmmm",this.state.topics) //returns [] the first render, returns ['topic1','topic2','topic3'] on the second render;
      const myJSON = JSON.stringify(this.state.topics);
     /* if(this.state.topics.length>0){
console.log("in if")
      this.state.topics.map(topic => (
          console.log(topic['$']["ActNumber"])
      ))}*/
      console.log("in render",this.state.err)
      if(this.state.topics2.length>0)
      return(
        <TableContainer component={Paper}>
        <Table aria-label="collapsible table">
          <TableHead>
            <TableRow>
              <TableCell />
              <TableCell align="right"></TableCell>
              <TableCell align="right">" </TableCell>
              
            </TableRow>
          </TableHead>
          <TableBody text-align="right">
            {this.state.topics2.map((row) => (
              <Row text-align="right" key={row.TrusteeName} row={row} />
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      
      )
      return <Alert err={this.state.err}>{this.state.err}</Alert>
    }
  }
  function Row(props) {
    const { row } = props;
    const [open, setOpen] = React.useState(false);
    const [activities, setActivities] = React.useState({});
  console.log("row",row.TrusteeName)
    return (
      <React.Fragment>
        <TableRow  sx={{ '& > *': { borderBottom: 'unset' } }}>
          <TableCell width="10%" >
            <IconButton
              aria-label="expand row"
              size="small"
              onClick={() => {
                console.log("onclick")
                setOpen(!open)
               
                }}
            >
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </TableCell>
          
          <TableCell align="right">{row.TrusteeName}</TableCell>
          <TableCell align="right">{120}</TableCell>
          
        </TableRow>
        <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
            <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell  align="right"></TableCell>
                    <TableCell  align="right"></TableCell>
                    <TableCell align="right">' </TableCell>
                    <TableCell align="right"></TableCell>
                    <TableCell align="right"></TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row.act.map((act) => (
                    <TableRow key={act.ActNumber}>
                      <TableCell  align="right" component="th" scope="row">
                        {act.Name}
                      </TableCell>
                      <TableCell  align="right">{act.Date.slice(0,9)}</TableCell>
                      <TableCell align="right">{act.Week}</TableCell>
                      <TableCell align="right">
                        {act.Sum}
                      </TableCell>
                      <TableCell align="right">
                        {act.StatusDesc}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
              
            </Collapse>
          </TableCell>
        </TableRow>
      </React.Fragment>
    );
  }





























