
import React, { useState } from 'react'
import  { Component } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import PropTypes from 'prop-types';
import { red } from 'material-ui-colors';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import ErorList from './errorlist'
import configData from "../src/config.json";
import XMLParser from 'react-xml-parser';


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
   
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  bgc_red:{
 backgroundColor: red['A700'],
 color:'white'
  }
  
}));

async function loginUser(credentials) {
  console.log("credentials",credentials)
 return fetch(configData.server+'login', {
   method: 'POST',
   headers: {
     'Content-Type': 'application/json'
   },
   body: JSON.stringify(credentials)
 })
   .then(data => data.json())

}
async function getteamleaders(credentials) {
  console.log("credentials",credentials)
 return fetch(configData.server+'getteamleaders', {
   method: 'POST',
   headers: {
     'Content-Type': 'application/json'
   },
   body: JSON.stringify(credentials)
 })
   .then(data => data.json())

}
export default function Login(props) {
  const classes = useStyles();
  const [username, setUserName] = useState();
  const [password, setPassword] = useState();
  const [err, setErr] = useState();

  const handleSubmit = async e => {
    e.preventDefault();
    let token=null
    let teamleaders=null
    try{  token = await loginUser({
      username,
      password
    });}catch(error){
     console.log("error catch1111",error)
      token=error
      setErr(error)
    } 

    var tokencut=token.token.split(";");
    var tokennum=tokencut[1]
    try{  teamleaders = await getteamleaders({
      tokennum,
    });}catch(error){
     console.log("error catch1111",error)
     teamleaders=error
     setErr(error)
    } 
    console.log(token,"token after catchhhhhh")
    if(token.token.includes('Error'))
    setErr(token.token)
    else{
      setErr(null)
    }
    var tokencutelement=[]
    tokencutelement['token']=tokencut
 
    var xml = new XMLParser().parseFromString(teamleaders.teamleaders); 
    console.log(xml.children[101])
  console.log("teamleaders")
    tokencutelement['teamleaders']=xml.children
    props.setToken({token:tokencutelement});
    console.log("after cat")
  }
console.log("err in log in",err)
  return (
    <Container component="main" maxWidth="xs" align="right">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar,classes.bgc_red}  >
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
         
        </Typography>
        <form onSubmit={handleSubmit} className={classes.form} noValidate>
          <TextField
          onChange={e => setUserName(e.target.value)}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label=" "
            name="email"
            autoComplete="email"
            autoFocus
          />
          <TextField
          style={{outlinecolor: red['A700']}}
         onChange={e => setPassword(e.target.value)}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label=""
            type="password"
            id="password"
            autoComplete="current-password"
          />
         
           <Stack sx={{ width: '100%' }} spacing={2}>  
           {props.err&&props.err.length>0?
           <ErorList err={props.err}  severity="error">{props.err}</ErorList>
            :null}
            {err?
           <ErorList  err={err} severity="error"></ErorList>
            :null}
          <Button
             margin="normal"
            type="submit"
            fullWidth
            variant="contained"
           
            className={classes.submit,classes.bgc_red}
          >
            
          </Button>
        </Stack>
        </form>
      </div>
      
    </Container>
  );
}
Login.propTypes = {
  setToken: PropTypes.func.isRequired
};