import React, { Component } from 'react';
export default class Topics extends Component{
    constructor(props){
      super(props);
      this.state = {
        topics: []
      }
    }
  
    componentDidMount() {
      fetch('http://localhost:8080/getBonusActivities', {
        method: 'POST',
        
      }).then((res)=>res.json().then((data)=>{
        this.setState({ topics: data.activities['Activities']['Activity'] });
        this.render()
      }));
      console.log(this.state.topics) //returns [];
    }
  
    render() {
      console.log("mmmmmmmmmmmmmmm",this.state.topics) //returns [] the first render, returns ['topic1','topic2','topic3'] on the second render;
      const myJSON = JSON.stringify(this.state.topics);
      if(this.state.topics.length>0){
console.log("in if")
      this.state.topics.map(topic => (
          console.log(topic['$']["ActNumber"])
      ))}
      return(
          
        <ul>
        {this.state.topics.length>0? this.state.topics.map(topic => (
          <li>{topic['$']['ActNumber']}</li>
        )):0}
      </ul>
      )
    }
  }